import React from "react";
import axios from "axios";
import {
  StyleSheet,
  Text,
  View,
  TextInput,
  Button,
  Image,
  TouchableOpacity,
} from "react-native";

class TAF extends React.Component {
  state = {
    TravelReqId: 0,
    EmployeeName: "",
    SiteLocation: "",
    SectorFunction: "",
    Department: "",
    Manager: "",
    ReqStatus: "",
    Status: "",
  };
  reqIdChange = (text) => {
    this.setState({ TravelReqId: text });
  };
  componentDidMount = () => {};
  approveRequest = (event) => {
    event.preventDefault();
    if (this.state.TravelReqId === 0) {
      alert("Please input a valid travel request Id to approve.");
      return false;
    }

    let updateObj = {
      ID: this.state.TravelReqId,
      ReqStatus_Id: 3,
    };

    axios
      .post(
        "https://dapps.in.moog.com/TAFWebApi/api/taf/UpdateTravelRequest/",
        updateObj
      )
      .then((response) => {
        if (response.data === "success") {
          alert("Update Successful!");
        } else {
          alert("Issue while updating.");
        }
      })
      .catch(function (error) {
        alert("error while updating the request : " + error);
      });
  };
  rejectRequest = (event) => {
    event.preventDefault();
    if (this.state.TravelReqId === 0) {
      alert("Please input a valid travel request Id to reject.");
      return false;
    }

    let updateObj = {
      ID: this.state.TravelReqId,
      ReqStatus_Id: 4,
    };
    let axiosConfig = {
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Origin": "https://10.91.160.100:19006/",
      },
    };

    axios
      .post(
        "https://dapps.in.moog.com/TAFWebApi/api/taf/UpdateTravelRequest/",
        updateObj,
        axiosConfig
      )
      .then((response) => {
        if (response.data === "success") {
          alert("Update Successful!");
        } else {
          alert("Issue while updating.");
        }
      })
      .catch(function (error) {
        alert("error while updating the request : " + error);
      });
  };
  getReqDetails = () => {
    if (this.state.TravelReqId === 0) {
      alert("Please input a valid travel request Id.");
      return false;
    }
    axios
      .get(
        "https://dapps.in.moog.com/TAFWebApi/api/taf/GetTravelRequestDetails?travelReqId=" +
          this.state.TravelReqId
      )
      .then((response) => {
        let reqDetails = response.data[0];
        this.setState({
          EmployeeName: reqDetails.EmployeeName,
          SiteLocation: reqDetails.SiteLocation,
          SectorFunction: reqDetails.SectorFunction,
          Department: reqDetails.Department,
          Manager: reqDetails.SupervisorName,
          ReqStatus: reqDetails.ReqStatus,
        });
        alert("Request Successful!");
      })
      .catch(function (error) {
        alert("Error while getting request details : " + error);
      });
  };
  render() {
    return (
      <View style={styles.container}>
        <View style={styles.headerView}>
          <Text style={styles.headerToolName}>Travel Authorization Form</Text>
          <Image
            style={styles.headerLogo}
            source={require("./Moog-Logo.png")}
          />
        </View>
        <Text style={styles.header}>Travel Request Details</Text>

        <Text style={styles.label}>Travel Request Id</Text>
        <TextInput
          style={styles.inputTxt}
          onChangeText={this.reqIdChange}
          keyboardType={"numeric"}
        ></TextInput>

        <Text style={styles.label}>Employee Name</Text>
        <TextInput
          value={this.state.EmployeeName}
          style={styles.inputTxt}
        ></TextInput>

        <Text style={styles.label}>Site Location</Text>
        <TextInput
          value={this.state.SiteLocation}
          style={styles.inputTxt}
        ></TextInput>

        <Text style={styles.label}>Sector Function</Text>
        <TextInput
          value={this.state.SectorFunction}
          style={styles.inputTxt}
        ></TextInput>

        <Text style={styles.label}>Department</Text>
        <TextInput
          value={this.state.Department}
          style={styles.inputTxt}
        ></TextInput>

        <Text style={styles.label}>Manager</Text>
        <TextInput
          value={this.state.Manager}
          style={styles.inputTxt}
        ></TextInput>

        <Text style={styles.label}>Request Status</Text>
        <TextInput
          value={this.state.ReqStatus}
          style={styles.inputTxt}
        ></TextInput>

        <View style={{ paddingTop: 10 }}>
          <TouchableOpacity style={styles.button} onPress={this.getReqDetails}>
            <Text style={{ color: "white" }}>GET REQUEST DETAILS</Text>
          </TouchableOpacity>
        </View>

        <View style={{ paddingTop: 5 }}>
          <TouchableOpacity style={styles.button} onPress={this.approveRequest}>
            <Text style={{ color: "white" }}>APPROVE</Text>
          </TouchableOpacity>
        </View>

        <View style={{ paddingTop: 5 }}>
          <TouchableOpacity style={styles.button} onPress={this.rejectRequest}>
            <Text style={{ color: "white" }}>REJECT</Text>
          </TouchableOpacity>
        </View>

        {/* <ProgressBar
          style={{ borderRadius: 10, height: 10 }}
          trackColor="#D1E3F6"
        /> */}
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    paddingTop: 40,
  },
  headerView: {
    flexDirection: "row",
  },
  headerToolName: {
    color: "maroon",
    textAlign: "left",
    fontWeight: "bold",
    fontSize: 20,
  },
  headerLogo: {
    justifyContent: "flex-end",
    position: "absolute",
    right: 0,
  },
  header: {
    color: "#2296f3",
    textAlign: "center",
    fontWeight: "bold",
    paddingTop: 20,
    fontSize: 15,
  },
  button: {
    backgroundColor: "#2296f3",
    padding: 10,
    justifyContent: "center",
    alignItems: "center",
  },
  label: { fontWeight: "bold" },
  inputTxt: { borderColor: "skyblue", borderWidth: 1, height: 30, margin: 5 },
});

export default TAF;
